(function () {

    var VueConfig = {

        install: function(Vue) {
            // vue resource 
            if (Vue.http && Vue.cookie) {
                Vue.http.options.root = '/';

                Vue.http.interceptors.push(function (request, next) {
                    request.headers.set('Authorization', Vue.cookie.get('admin-token'));

                    next(function (response) {

                        if (response.headers.map.Authorization !== undefined) {
                            Vue.cookie.set('admin-token', response.headers.map.Authorization[0], {expires: '1h'});
                        }

                        // 
                        if (response.status === 401 && response.body.code !== 4010) {
                            window.location.href = '/admin/login.html';
                        } else if (response.status === 422) {
                            var errorObj = response.body.data;
                            for (var field in errorObj) {
                                for (var key in errorObj[field]) {
                                    this.$Notice.error({title: errorObj[field][key]})
                                }
                            }
                        } else if (response.status !== 200) {
                            this.$Message.error(response.data.msg);
                        }
                    });
                });
            }

            // debug
            Vue.config.debug = true;
        }
    };

    if (typeof exports == "object") {
        module.exports = VueConfig;
    } else if (typeof define == "function" && define.amd) {
        define([], function(){ return VueConfig; })
    } else if (window.Vue) {
        window.VueConfig = VueConfig;
        Vue.use(VueConfig);
    }

})();


